import React from 'react'
import ScreenHeader from './screen-header'
import ScreenFooter from './screen-footer'
import Slider from './slider'
import { trayList, footerItemList } from '../../../public/config/trayList'

interface TrayItemProps {
    iconType?: string,
    label: string,
    className: string,
    icon: string,
}
interface SliderProps {
    selectedTrayNo: number,
    trayList: {
        pageNo: number,
        items: TrayItemProps[]
    }[]
}

const config: SliderProps = {
    selectedTrayNo: 1,
    trayList
}

export default () => {
    return (
        <div className="screen">
            <ScreenHeader></ScreenHeader>
            <Slider config={config}></Slider>
            <ScreenFooter footerItemList={footerItemList}></ScreenFooter>
        </div>
    )
}

export {
    SliderProps,
    TrayItemProps
}