import React, { useEffect } from 'react'
import { Link } from 'react-router-dom';
import { Tabs } from 'antd';
import { Pane } from './App';
import { Conditional } from './Conditional';
import TodoApp from '../features/todos/components/App'

const { TabPane } = Tabs;

interface TabKeepAliveProps {
    activeKey: string,
    setActiveKey: (activeKey: string) => void
    panes: Pane[],
    setPanes: (panes: Pane[]) => void,
    onChange: (activeKey: string) => void,
    onEdit: (targetKey: string, action: 'add' | 'remove') => void
}


class ConditionalApp extends React.Component {
    state = {
        active: true
    }

    toggle = () => {
        this.setState({
            active: !this.state.active
        })
    }

    render(): React.ReactNode {
        return <div>
            <button onClick={this.toggle}>toggle</button>
            <Conditional active={this.state.active}>
                <TodoApp></TodoApp>
            </Conditional>
        </div>
    }
}

export default class TabKeepAlive extends React.Component<TabKeepAliveProps, { active: boolean }> {
    constructor(props: TabKeepAliveProps) {
        super(props);
    }

    render() {
        const { activeKey, panes, onChange, onEdit } = this.props
        // console.log(activeKey)
        return (
            <div>
                {/* {
                    Array.from({ length: 2 }).map((item, index) => <ConditionalApp key={index}></ConditionalApp>)
                } */}
                <Tabs
                    hideAdd
                    onChange={onChange}
                    activeKey={activeKey}
                    type="editable-card"
                    onEdit={onEdit}
                >
                    {panes.map(pane => (
                        <TabPane tab={<Link to={pane.to}>{pane.title}</Link>} key={pane.key}>
                            {/* 由于antd Tabs组件在切换 tab 时对应组件只是显示隐藏，我们需要创建组件 卸载/重建 上下文，所以改用路由来实现 */}
                            {/* <pane.content></pane.content> */}
                        </TabPane>
                    ))}
                </Tabs>
            </div>
        );
    }
}