import React from 'react'
import Signal from './signal'
import Battery from './battery'


const ScreenHeader = () => {
    const date = new Date()
    return <div className="screen-header">
        <Signal></Signal>
        <div className="time-indicator">{`${date.getHours()}:${date.getMinutes()}`}</div>
        <Battery></Battery>
    </div>
}

export default ScreenHeader