import React, { useState } from 'react';
import { Button } from 'antd';
import FormRender, { Error, useForm } from 'form-render';


const schema = {
    type: 'object',
    properties: {
        input1: {
            title: '简单输入框',
            type: "string",
            required: true,
        },
        select1: {
            title: '单选',
            type: "string",
            enum: ['a', 'b', 'c'],
            enumNames: ['早', '中', '晚'],
        },
    },
};

export const FormRenderApp: React.FC = () => {
    const form = useForm();
    const onFinish = (formData: any, errors: Error[]) => {
        console.log('formData:', formData, 'errors', errors);
    };
    return (
        <>
            {/* 验证form-render */}
            {/* <FormRender form={form} schema={schema} onFinish={onFinish} />
            <Button type="primary" onClick={form.submit}>
                提交
            </Button> */}
        </>
    );
};