import React, { useState } from "react"
import {
    BrowserRouter as Router,
    Routes,
    Route,
    Link
} from "react-router-dom"
import { Provider as KeepAliveProvider, KeepAlive } from 'react-keep-alive'
import { IntlProvider } from 'react-intl'
import messages from './locales/index'
import { Layout, Menu, ConfigProvider } from "antd"
// import ThemePicker from './layout/header'
// 上面省略/Index的导入方式会导致Header组件不能正常更新，什么原因？
import ThemePicker from '@src/layout/header/theme-picker/Index'
import LocalePicker, { localeList } from './layout/header/locale-picker/Index'
import { routes, RouteType } from './router'
import IPhone from "./iphone/App"
import 'antd/dist/antd.variable.min.css'
import './App.scss'


const { Sider, Content } = Layout;


export default function App() {
    const renderRoutes = (routes: RouteType[]) => {
        return <Routes>
            {
                routes.map((route, index) => (
                    <Route key={index} path={route.path} element={<route.component></route.component>} />
                ))
            }
        </Routes>
    }
    const [showHeader, setShowHeader] = useState(true)
    const [count, setCount] = useState(1)
    const [locale, setLocale] = useState(localeList[0])
    return (
        <IntlProvider locale={locale.key} messages={messages[locale.key]}>
            <ConfigProvider locale={locale.value}>
                <div className="app-container">
                    <div className="app-header">
                        <div className="event-test-box">
                            {/* <Button onClick={() => setShowHeader(!showHeader)}>toggle header</Button>
                            <Button onClick={() => document.dispatchEvent(new CustomEvent('hello'))}>dispatch 'hello' event</Button>
                            <Button onClick={() => setCount(count + 1)}>add</Button> */}
                        </div>
                        {
                            showHeader && <div className="toolbox">
                                <ThemePicker count={count} />
                                <LocalePicker locale={locale} setLocale={setLocale} />
                            </div>
                        }
                    </div>

                    <Router>
                        <Layout style={{ height: '100%' }}>
                            <Sider>
                                <Menu
                                    style={{ width: 200, height: '100%' }}
                                    // selectedKeys={[activeKey]}
                                    mode="inline"
                                >
                                    {routes.map(route => (
                                        <Menu.Item key={route.path}>
                                            <Link to={route.path}>{route.name}</Link>
                                        </Menu.Item>
                                    ))}
                                </Menu>
                            </Sider>
                            <Content style={{ margin: 20 }}>
                                {/* <KeepAliveProvider> */}
                                <div className="app-content">
                                    <Routes>
                                        {
                                            routes.map((route, index) => (
                                                <Route
                                                    key={index}
                                                    path={route.path}
                                                    element={
                                                        // <KeepAlive name={route.path}>
                                                        <route.component></route.component>
                                                        // </KeepAlive>
                                                    }>
                                                    <Route
                                                        index={true}
                                                        element={<IPhone></IPhone>}
                                                    ></Route>
                                                    {
                                                        route.routes && route.routes.map((subRoute, index) => {
                                                            // console.log(subRoute.path === 'iphone')
                                                            const isIndex = subRoute.path === 'iphone'
                                                            return (
                                                                <Route
                                                                    key={index}
                                                                    // index={isIndex}
                                                                    path={subRoute.path}
                                                                    element={
                                                                        // <KeepAlive name={subRoute.path}>
                                                                        <subRoute.component></subRoute.component>
                                                                        // </KeepAlive>
                                                                    }
                                                                />
                                                            )
                                                        })
                                                    }
                                                </Route>
                                            ))
                                        }
                                    </Routes>
                                </div>
                                {/* </KeepAliveProvider> */}
                            </Content>
                        </Layout>

                    </Router>
                </div>
            </ConfigProvider>
        </IntlProvider>
    )
}