import React, { useEffect, useState } from 'react';
import { Drawer, Button, DrawerProps } from 'antd';

const DrawerContent = () => {
    useEffect(() => {
        console.log('DrawerContent mounted....')
    }, [])
    return <div className="drawer-content">
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
    </div>
}
const MyDrawer = (props: DrawerProps) => {
    useEffect(() => {
        console.log('Drawer mounted....')
    }, [])
    // 如果forceRender，那么DrawerContent开始就会被mounted，否则只有Drawer可见的时候，DrawerContent才会被mounted
    return <Drawer  {...props}>
        <DrawerContent />
    </Drawer>
}

export const DrawerWidth: React.FC = () => {
    const [visible, setVisible] = useState(false);
    const showDrawer = () => {
        setVisible(true);
    };
    const onClose = () => {
        setVisible(false);
    };
    return (
        <>
            {/* 体会下面两种选择器的区别 */}
            <div className="hello world">UP</div>
            <div className="hello">
                <div className="world">Down</div>
            </div>
            <Button type="primary" onClick={showDrawer}>
                Open
            </Button>
            <MyDrawer
                title="Basic Drawer"
                placement="right"
                visible={visible}
                // forceRender
                onClose={onClose}
            />
        </>
    );
};