import React, { useState } from 'react';
import { Modal, Button } from 'antd';

const App = () => {
    const [isModalVisible, setIsModalVisible] = useState(false);

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    return (
        <>
            <h1>本示例尝试通过getContainer控制modal蒙层遮盖范围，但是蒙层遮盖范围是通过样式控制的，默认全屏覆盖</h1>
            <Button type="primary" onClick={showModal}>
                Open Modal
            </Button>
            <Modal
                title="Basic Modal"
                visible={isModalVisible}
                // 并不能控制蒙层遮盖范围
                getContainer={() => {
                    return document.querySelector('.ant-layout-content')
                }}
                // 通过样式控制蒙层覆盖范围
                maskStyle={{ left: 200, top: 42 }}
                onOk={handleOk}
                onCancel={handleCancel}
            >
                <p>Some contents...</p>
                <p>Some contents...</p>
                <p>Some contents...</p>
            </Modal>
        </>
    );
};

export default App;