import React, { useEffect, useState } from 'react'
import { ConfigProvider, Menu, Dropdown } from 'antd'
import { DownOutlined } from '@ant-design/icons'
import './Index.css'

const colorList = ['#6B96BD', '#685CF0', '#149CA8', '#B3894F']

function ThemePicker({ primaryColor, setPrimaryColor }: { primaryColor: String, setPrimaryColor: Function }) {
    const defaultSelectedKeys = [colorList.findIndex(item => item === primaryColor).toString()]
    return (
        <Menu defaultSelectedKeys={defaultSelectedKeys}>
            {
                colorList.map((item, index) => (
                    <Menu.Item key={index} onClick={() => setPrimaryColor(item)}>
                        <div className="theme-picker-item" style={{ color: item }}>
                            <span
                                className="theme-picker-item-icon"
                                style={{
                                    backgroundColor: item
                                }}
                            ></span>
                            <span className="theme-picker-item-value">{item}</span>
                        </div>
                    </Menu.Item>
                ))
            }
        </Menu>
    )
}

export default function (props: { count: number }) {
    const [primaryColor, setPrimaryColor] = useState(colorList[0])

    useEffect(() => {
        ConfigProvider.config({
            theme: {
                primaryColor
            },
        });
    }, [primaryColor])

    useEffect(() => {
        const handler = (event: CustomEvent) => {
            console.log('hello')
        }
        document.addEventListener("hello", handler)
        return () => {
            document.removeEventListener("hello", handler)
        }
    }, [])

    return (
        <Dropdown
            overlay={() => <ThemePicker primaryColor={primaryColor} setPrimaryColor={setPrimaryColor} />}
            trigger={['click']}
        >
            <div className="ant-dropdown-button">
                切换主题 <DownOutlined />
            </div>
        </Dropdown>
    )
}