import React from 'react'
import { FormattedMessage } from 'react-intl'
import { DatePicker, TimePicker, Table } from 'antd'

const dataSource = [
    {
        key: '1',
        name: '胡彦斌',
        age: 32,
        address: '西湖区湖底公园1号',
    },
    {
        key: '2',
        name: '胡彦祖',
        age: 42,
        address: <FormattedMessage id='address' />,
    },
];

const columns = [
    {
        title: '姓名',
        dataIndex: 'name',
        key: 'name',
    },
    {
        title: '年龄',
        dataIndex: 'age',
        key: 'age',
    },
    {
        title: '住址',
        dataIndex: 'address',
        key: 'address',
    },
];
export default function () {

    return (
        <div>
            <DatePicker />
            <TimePicker />
            <Table dataSource={dataSource} columns={columns} />
        </div>
    )
}