import React, { useRef } from 'react';
import { Tabs, Button } from 'antd';
import Icon from './Index.svg'
import './Index.less'
import { InitialValues } from './InitialValue';
import { SearchableSelect } from './SearchableSelect';
import { UploadAccept } from './UploadAccept';
import { DrawerWidth } from './DrawerWidth';
import { FormRenderApp } from './FormRenderApp';
import { DynamicColumnTable } from './DynamicColumnTable';

const { TabPane } = Tabs

const Demo = () => {
    return (
        <div className='form-designer-container'>
            {/* 验证react svg */}
            {/* <Icon /> */}

            <Tabs defaultActiveKey="4">
                <TabPane tab="Initial Value" key="1">
                    <InitialValues />
                </TabPane>
                <TabPane tab="Searchable Select" key="2">
                    <SearchableSelect />
                </TabPane>
                <TabPane tab="Upload Accept" key="3">
                    <UploadAccept />
                </TabPane>
                <TabPane tab="Drawer Width" key="4">
                    <DrawerWidth />
                </TabPane>
                <TabPane tab="Form Render" key="5">
                    <FormRenderApp />
                </TabPane>
                <TabPane tab="Dynamic Column Table" key="6">
                    <DynamicColumnTable />
                </TabPane>
            </Tabs>
        </div>
    );
};

export default Demo;