import React, { useState } from 'react'
import { Link, Outlet, useLocation } from 'react-router-dom';
import { Layout, Menu } from 'antd';
import _ from 'lodash'
import TabKeepAlive from './TabKeepAlive';
import { routes, RouteType } from '../router'
import { LoadableComponent } from '@loadable/component';

const { Sider, Content } = Layout;

export interface Pane {
    key: string,
    title: string,
    to: string,
    content: LoadableComponent<unknown>
}

interface MenuInfo {
    key: string;
    keyPath: string[];
    /** @deprecated This will not support in future. You should avoid to use this */
    item: React.ReactInstance;
    domEvent: React.MouseEvent<HTMLElement> | React.KeyboardEvent<HTMLElement>;
}

const paneList: Pane[] = routes[0].routes.map(({ path, component }: RouteType, index: number) => {
    const result = {
        key: index.toString(),
        title: path.includes('-') ? path.split('-').map(item => _.capitalize(item)).join(' ') : path,
        to: path,
        content: component
    }
    // console.log(result)
    return result
})

export default function App() {
    const activeRoute = useLocation().pathname.slice(1)
    const activePane = paneList.find(pane => pane.to === activeRoute) || paneList[0]
    const [activeKey, setActiveKey] = useState(activePane.key)
    const [panes, setPanes] = useState([activePane])
    // console.log(useLocation())
    // console.log(activePane)

    const handleClick = (e: MenuInfo) => {
        // console.log('click ', e);
        add(e.key)
    };

    const onChange = (activeKey: string) => {
        setActiveKey(activeKey)
    };

    const onEdit = (targetKey: string, action: 'add' | 'remove') => {
        // this[action](targetKey);
        if (action === 'add') {
            add(targetKey)
        } else {
            remove(targetKey)
        }
    };

    const add = (activeKey: string) => {
        setActiveKey(activeKey)
        if (!panes.find(pane => pane.key === activeKey)) {
            setPanes([...panes, paneList.find(pane => pane.key === activeKey)])
        }
    };

    const remove = (targetKey: string) => {
        let newActiveKey;
        let lastIndex: number;
        panes.forEach((pane, i: number) => {
            if (pane.key === targetKey) {
                lastIndex = i - 1;
            }
        });
        const newPanes = panes.filter(pane => pane.key !== targetKey);
        if (newPanes.length && activeKey === targetKey) {
            if (lastIndex >= 0) {
                newActiveKey = newPanes[lastIndex].key;
            } else {
                newActiveKey = newPanes[0].key;
            }
        }
        setActiveKey(newActiveKey)
        setPanes(newPanes)
    };

    return (
        <Layout style={{ height: '100%' }}>
            <Sider>
                <Menu
                    onClick={handleClick}
                    style={{ width: 200, height: '100%' }}
                    selectedKeys={[activeKey]}
                    mode="inline"
                >
                    {paneList.map(pane => (
                        <Menu.Item key={pane.key}>
                            <Link to={pane.to}>{pane.title}</Link>
                        </Menu.Item>
                    ))}
                </Menu>
            </Sider>
            <Content style={{ margin: '0 20px' }}>
                <TabKeepAlive
                    activeKey={activeKey}
                    setActiveKey={setActiveKey}
                    panes={panes}
                    setPanes={setPanes}
                    onChange={onChange}
                    onEdit={onEdit}
                ></TabKeepAlive>
                {/* 这里一定要放一个<Outlet>，它就像vue router里边的 router-view */}
                <Outlet></Outlet>
            </Content>
        </Layout>
    );
}