import React, { useEffect } from 'react';
import BaiduMap from './baidu-map.svg'
import {
    append,
    appendTo,
    classes,
    create,
    innerSVG
} from 'tiny-svg';

export default () => {
    useEffect(() => {
        var container = document.querySelector('.tiny-svg') as unknown;
        var element = appendTo(create('svg'), container as SVGElement);

        var g = appendTo(create('g'), element as SVGElement);

        // add classes, SVG style!
        classes(g).add('foo');

        var text = `
            <g class="foo bar">
                <rect x="0" y="0" width="0" height="0" rx="50" ry="50"/>
            </g>
        `;

        // set innerSVG
        innerSVG(g, text);

        const icon = create('image', {
            width: 20,
            height: 20,
            href: BaiduMap
        })
        append(document.querySelector('.foo.bar'), icon as SVGElement)
    })

    return <div className="tiny-svg">

    </div>
}