import React from 'react'
import { TrayItemProps } from './screen'

const TrayItem = (props: {
    data: TrayItemProps
}) => {
    const { data } = props
    return <div className="tray-item">
        <div className={`tray-item-icon ${data.iconType === 'circle' ? 'circle' : ''}`} style={{ backgroundImage: `url(/images/icons/${data.icon})` }}></div>
        {
            data.label && <div className="tray-item-label">{data.label}</div>
        }
    </div>
}

export default TrayItem