import React from 'react'
import { Anchor } from 'antd';
import './Index.less'

const { Link } = Anchor;


export default () => {
    const getCurrentAnchor = () => '#components-anchor-demo-static';
    return <div className="anchor-demo-container">
        <div className="anchor-content">
            <div id="components-anchor-demo-basic">basic</div>
            <div id="components-anchor-demo-static">static</div>
            <div id="API">
                <div id="Anchor-Props">Anchor-Props</div>
                <div id="Link-Props">Link-Props</div>
            </div>
        </div>
        <Anchor
        // getCurrentAnchor={getCurrentAnchor}
        >
            <Link href="#components-anchor-demo-basic" title="Basic demo" />
            <Link href="#components-anchor-demo-static" title="Static demo" />
            <Link href="#API" title="API">
                <Link href="#Anchor-Props" title="Anchor Props" />
                <Link href="#Link-Props" title="Link Props" />
            </Link>
        </Anchor>
    </div>
}