import React from 'react'
import TrayItem from './tray-item'
import { TrayItemProps } from './screen'

export interface ScreenFooterProps {
    footerItemList: TrayItemProps[]
}

const ScreenFooter = (props: ScreenFooterProps) => {
    const { footerItemList } = props
    // console.log(footerItemList)
    return <div className="screen-footer">
        {
            footerItemList.map((item, index) => <TrayItem key={index} data={item}></TrayItem>)
        }
    </div>
}

export default ScreenFooter