import React from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { setVisibilityFilter } from '../todoSlice'

interface LinkProps {
  filter: string,
  children: React.ReactNode
}
const Link = ({ filter, children }: LinkProps) => {
  const visibilityFilter = useSelector((state: { todoApp: { visibilityFilter: string } }) => state.todoApp.visibilityFilter)
  const dispatch = useDispatch()
  if (filter === visibilityFilter) {
    return <span>{children}</span>
  }

  return (
    <a
      href=""
      onClick={e => {
        e.preventDefault()
        dispatch(setVisibilityFilter(filter))
      }}
    >
      {children}
    </a>
  )
}

export default Link