import { Button } from 'antd'
import React, { createRef, forwardRef, MutableRefObject, ReactNode, useCallback, useEffect, useImperativeHandle, useRef, useState } from 'react'

interface GrandsonProps {
    grandsonRef: React.Ref<ReactNode>
}

const Grandson: React.FC<GrandsonProps> = ({ grandsonRef }) => {
    useEffect(() => {
        console.log('grandson mounted....')
    }, [])
    const sayHello = () => {
        console.log('I am your grandson...')
    }
    return <div className="grandson">
        <h5>Grandson123</h5>
        <input ref={grandsonRef} type="text" />
    </div>
}

interface SonProps {
    label: string
}


const Son: React.FC<SonProps> = forwardRef((props, ref) => {
    // Function components cannot be given refs. Attempts to access this ref will fail. Did you mean to use React.forwardRef()?
    const { label } = props
    const [value, setValue] = useState('')

    useImperativeHandle(ref, () => ({
        getValue
    }))

    const handleChange = (e: any) => {
        const value = e.target.value
        setValue(value)
    }

    const getValue = useCallback(() => {
        return value
    }, [value])

    return <div className="son">
        <h4>Son</h4>
        <span>{label}</span>
        <input type="text" onChange={handleChange} />
    </div>
})

const Parent = () => {
    const childRef = useRef({ getValue: Function })

    const handleFocus = () => {
        const node = childRef.current
        console.log(node.getValue())
    }
    return <div className="parent1234">
        <h3>Parent</h3>
        <Son label='儿ff啊' ref={childRef} />
        <button onClick={handleFocus}>focus</button>
    </div>
}

export default Parent