import React from 'react'
import TrayItem from './tray-item'
import { SliderProps } from './screen'


const Slider = (props: { config: SliderProps }) => {
    // console.log(props)
    const { selectedTrayNo, trayList } = props.config
    return <div className="slider">
        <div className="tray">
            {
                trayList.find(item => item.pageNo === selectedTrayNo).items.map((item, index) => <TrayItem key={index} data={item}></TrayItem>)
            }
        </div>
        <div className="tray-indicator">
            {trayList.map(item => <div key={item.pageNo} className={`tray-indicator-item ${item.pageNo === selectedTrayNo ? 'active' : ''}`} />)}
        </div>
    </div>
}

export default Slider