import React, { useState, useRef, useLayoutEffect, ReactNode } from 'react'
import ReactDOM from 'react-dom'

export const Conditional: React.FC<{ active: boolean, children?: ReactNode }> = (props) => {
    const [targetElement] = useState(() => document.createElement('div'))
    const containerRef = useRef()
    useLayoutEffect(() => {
        if (props.active) {
            (containerRef.current as HTMLDivElement).appendChild(targetElement)
        } else {
            try {
                (containerRef.current as HTMLDivElement).removeChild(targetElement)
            } catch (e) { }
        }
    }, [props.active])
    return (
        <>
            <div ref={containerRef} />
            {ReactDOM.createPortal(props.children, targetElement)}
        </>
    )
}
