import React from 'react'
import { Menu, Dropdown } from 'antd'
import { DownOutlined } from '@ant-design/icons'
import { Locale } from 'antd/lib/locale-provider'
import enUS from 'antd/lib/locale/en_US'
import zhCN from 'antd/lib/locale/zh_CN'
import zhTW from 'antd/lib/locale/zh_TW'

type LocalKey = 'en' | 'zh-CN' | 'zh-TW'

interface LocaleInterface {
    name: string,
    key: LocalKey,
    value: Locale
}

interface LocalePickerProps {
    locale: LocaleInterface,
    setLocale: Function
}

export const localeList: LocaleInterface[] = [
    {
        name: '英文',
        key: 'en',
        value: enUS
    },
    {
        name: '中文简体',
        key: 'zh-CN',
        value: zhCN
    },
    {
        name: '中文繁体',
        key: 'zh-TW',
        value: zhTW
    }
]



function LocalePicker({ locale, setLocale }: LocalePickerProps) {
    const defaultSelectedKeys = [localeList.findIndex(item => item.key === locale.key).toString()]
    const onLocalChange = (locale: LocaleInterface) => {
        setLocale(locale)
    }
    return (
        <Menu defaultSelectedKeys={defaultSelectedKeys}>
            {
                localeList.map((item, index) => (
                    <Menu.Item key={index} onClick={() => onLocalChange(item)}>
                        <div className="locale-picker-item">
                            {item.name}
                        </div>
                    </Menu.Item>
                ))
            }
        </Menu>
    )
}

export default function ({ locale, setLocale }: LocalePickerProps) {
    return (
        <Dropdown
            overlay={() => <LocalePicker locale={locale} setLocale={setLocale} />}
            trigger={['click']}
        >
            <div className="ant-dropdown-button">
                切换语言 <DownOutlined />
            </div>
        </Dropdown>
    )
}