export const DynamicColumnTable = () => {
    const [type, setType] = useState('fire')
    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            render: (text: string) => <a>{text}</a>,
        },
        {
            title: 'Age',
            dataIndex: 'age',
            key: 'age',
        },
        {
            title: 'Address',
            dataIndex: 'address',
            key: 'address',
        },
        {
            title: 'Tags',
            key: 'tags',
            dataIndex: 'tags',
            render: (tags: string[]) => (
                <>
                    {tags.map(tag => {
                        let color = tag.length > 5 ? 'geekblue' : 'green';
                        if (tag === 'loser') {
                            color = 'volcano';
                        }
                        return (
                            <Tag color={color} key={tag}>
                                {tag.toUpperCase()}
                            </Tag>
                        );
                    })}
                </>
            ),
        },
        {
            title: 'Action',
            key: 'action',
            render: (text: string, record: Record) => {
                return <Space size="middle">
                    <a>Invite {record.name}</a>
                    <a>{type}</a>
                </Space>
            },
        },
    ];

    const onChange = (pagination: TablePaginationConfig) => {
        // pagination包括current和pageSize
        console.log(pagination)
    }
    return <>
        <Button type='primary' onClick={() => setType('keep')}>change type</Button>
        <Table columns={columns} dataSource={data} onChange={onChange} />
    </>
}