import React, { useEffect, useState } from 'react';
import { Form, Input, Button, Checkbox, FormInstance, Cascader, Select, DatePicker } from 'antd';
import { Moment } from 'moment';

const { Option } = Select
const { RangePicker } = DatePicker


const UserName: React.FC<{ form: FormInstance, username: string }> = ({ form, username }) => {
    // useEffect(() => {
    //     form.setFieldsValue({
    //         username
    //     })
    // }, [username])
    return <Form.Item
        label="Username"
        name="username"
        rules={[{ required: true, message: 'Please input your username!' }]}
    >
        <Input />
    </Form.Item>
}

